  <v-container fluid fill-height style="padding:0;" id="xhr" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="readystatechange">使用readystatechange监听XMLHttpRequest请求的状态</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <pre class="code">{{pres[0]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="request">定义请求数据的监听事件</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <pre class="code">{{pres[1]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="upload">定义上传数据的监听事件</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <pre class="code">{{pres[2]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="arraybuffer">请求二进制数据</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <pre class="code">{{pres[3]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      pres: 
[
    `
    const xhr = new XMLHttpRequest();
    xhr.onreadystatechange = function () {
        if (xhr.readyState === XMLHttpRequest.UNSENT) {
            console.log("XMLHttpRequest创建完成")
        } else if(xhr.readyState === XMLHttpRequest.OPENED) {
            console.log("open方法调用了");
            // header等信息需要在这里设置，否则不生效
            xhr.setRequestHeader('enctype', 'text/plain');
            xhr.timeout = 2000;
        } else if(xhr.readyState === XMLHttpRequest.HEADERS_RECEIVED) {
            console.log("send方法调用了");
        } else if (xhr.readyState === XMLHttpRequest.LOADING) {
            console.log("正在接受返回的数据")
        } else if (xhr.readyState === XMLHttpRequest.DONE && xhr.status === 200) {
            console.log("请求完成 -> " + xhr.statusText);

            /*
                1. xhr.response 获取响应数据
                2. xhr.responseText 获取响应的文本
                3. xhr.statusText 返回状态文本 OK
                4. xhr.status 状态码 200
                5. xhr.abort(); 终止请求
                6. 获取响应头键值对
                    const headers = xhr.getAllResponseHeaders();
                    const headersArr = headers.trim().split(/[\\r\\n]+/);
                    headersArr.forEach(item => {
                        console.log(item.split(': ')[0], item.split(': ')[1]);
                    })
                7. xhr.getResponseHeader("Content-Type"); 获取指定响应头
                8. 打开一个请求
                    xhr.open(
                        "GET", // "POST", "PUT", "DELETE"
                        url,
                        true // 异步，false为同步
                    )
                9. xhr.send(); // 发送请求，如果是post请求send方法传入的参数表示传递的请求体数据
            */
        }
    };

    xhr.open("GET", "/api/test");
    xhr.send();
    `,
    `
    xhr.addEventListener("load", () => {
        // 请求完成
        console.log(xhr.response);
    });

    xhr.addEventListener("progress", (e) => {
        if (e.lengthComputable) {
            const percentComplete = (e.loaded / e.total * 100).toFixed(2);
            console.log('下载的进度 -> ' + percentComplete);
        } else {
            console.log('下载的资源大小未知')
        }
    });

    xhr.addEventListener("error", (e) => {
        console.log("请求出错");
    });

    xhr.addEventListener("abort", (e) => {
        console.log("请求被用户终止");
    });

    xhr.addEventListener("loadend", (e) => {
        // abort, load, 和 error 事件的并集
        console.log("请求结束");
    });
    `,
    `
    xhr.upload.addEventListener("load", () => {
        console.log('资源上传完成');
    });

    xhr.upload.addEventListener("progress", (e) => {
        if (e.lengthComputable) {
            const percentComplete = (e.loaded / e.total * 100).toFixed(2);
            console.log('上传的进度 -> ' + percentComplete);
        } else {
            console.log('上传资源大小未知');
        }
    });
    
    xhr.upload.addEventListener("error", (e) => {
        console.log('资源上传出错');
    });

    xhr.upload.addEventListener("abort", (e) => {
        console.log('上传被用户取消');
    });
    `,
    `
    const xhr = new XMLHttpRequest();
    xhr.addEventListener("load", () => {
        // 请求完成
        console.log(xhr.response);
    });

    xhr.responseType = "arraybuffer";

    xhr.open("GET", "/api/test");
    xhr.send();
    `
],
      time: "2019-07-15",
      title: "XMLHttpRequest",
      items: [
        {
          title: "readystatechange",
          target: "#readystatechange"
        },
        { divider: true },
        {
          title: "定义请求数据的监听事件",
          target: "#request"
        },
        { divider: true },
        {
          title: "定义上传数据的监听事件",
          target: "#upload"
        },
        { divider: true },
        {
          title: "请求二进制数据",
          target: "#arraybuffer"
        },
        { divider: true },
      ],
    };
  },
  mounted() {
  },
  methods: {}
};
</script>

